.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  body {
    margin: 0;
    padding: 0;
    background: black;
    overflow-y: scroll;
    --secondary: rgb(161, 161, 161);
    --divider: #343434;
  }
  
  * {
    box-sizing: border-box;
    font-family: ".SFNSText", "SFProText-Regular", "SFUIText-Regular", ".SFUIText",
      Helvetica, Arial, sans-serif;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  
  #root {
    display: flex;
    justify-content: center;
  }
  
  .screen {
    width: 100%;
    height: 100%;
  }
  
  .container {
    max-width: 1200px;
    overflow: hidden;
    
    flex: 1 1 100%;
  }
  
  h1 {
    font-weight: bold;
    color: white;
    margin: 6px 0 12px;
  }
  
  .date {
    color: var(--secondary);
    font-size: 14px;
    text-transform: uppercase;
  }
  
  header {
    border-bottom: 1px solid var(--divider);
    position: relative;
  }
  
  .avatar {
    background: var(--divider);
    border-radius: 50%;
    position: absolute;
    bottom: 12px;
    right: 0;
    overflow: hidden;
  }
  
  .avatar,
  .avatar img {
    width: 40px;
    height: 40px;
  }
  
  ul,
  li {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .card-list {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    margin-left: 16px;
    margin-right: 16px;
  }
  
  .card {
    position: relative;
    padding: 25px;
    height: 460px;
    flex: 0 0 40%;
    max-width: 40%;
  }
  
  .card:nth-child(4n + 1),
  .card:nth-child(4n + 4) {
    flex: 0 0 60%;
    max-width: 60%;
  }
  
  .card:nth-child(odd) {
    padding-left: 0;
  }
  
  .card:nth-child(even) {
    padding-right: 0;
  }
  
  .card-content-container {
    color: #9d9ca1;
    width: 100%;
    height: 100%;
    position: relative;
    display: block;

  }
  
  .card-content-container.open {
    top: 0;
    left: 0;
    right: 0;
    position: fixed;
    z-index: 2;
    overflow: hidden;
    padding: 40px 0;
  }
  
  .card-content {

    position: relative;
    border-radius: 20px;
    background: #1c1c1e;
    overflow: hidden;
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
  
  .open .card-content {
    height: auto;
    max-width: 700px;
    overflow: hidden;

  }
  
  .card-open-link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: scroll;
  }
  
  .card-image-container {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    height: 420px;
    width: 100vw;
  }
  
  .open .card-image-container,
  .open .title-container {
    z-index: 2;
  }
  
  .title-container {
    position: absolute;
    top: 15px;
    left: 15px;
    max-width: 300px;
  }
  
  .open .title-container {
    top: 30px;
    left: 30px;
  }
  
  h2 {
    color: #fff;
    margin: 8px 0;
  }
  
  .category {
    color: #fff;
    font-size: 14px;
    text-transform: uppercase;
  }
  
  .overlay {
    z-index: 2;
    position: fixed;
    background: rgba(0, 0, 0, 0.8);
    will-change: opacity;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;

  }
  
  .overlay a {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100vw;
    left: 50%;
  
    transform: translateX(-50%);
  }
  
  .content-container {
    padding: 460px 35px 35px 35px;
    max-width: 700px;
    width: 90vw;
    overflow: scroll;
  }
  
  

  .hometoptext2 {
    font-size: 70px;
  }

  .hometopsidetext{
    color:white;
    font-size: 25px;
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
  }

  .homecardliTitle{
    font-size: 36px;
  }

  .homecardli{
    font-size: 24px;
  }

  p {
    color: #c4c4c4;
    font-size: 1.6rem;
    line-height: 20px;
  }

.DevIntroduce{
  color: #c4c4c4;
  font-size: 20px;
  margin-top: 16px;
  line-height: 20px;
}

  @media only screen and (max-width: 800px) {
    

    

    


    .homecardli{
      font-size: 16px;
    }
    .hometoptext2 {
      font-size: 8vw;
    }
    .card {
      flex: 0 0 50%;
      max-width: 50%;
    }
  
    .card:nth-child(4n + 1),
    .card:nth-child(4n + 4) {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .homecardliTitle{
      font-size: 20px;
    }
    p {
      font-size: 16px;
    }

    .DevIntroduce{
      font-size: 16px;
    }

    .devcard{
      max-width: ;
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
      transition: 0.3s;
      
    }


    .hometopsidetext{
      font-size: 15px;
      margin-top: 5px;
      margin-left: 10px;
      margin-right: 10px;
    }

    
    .card {
      flex: 1 0 100%;
      max-width: 100%;
      padding: 10px;
      padding-left: 0;
      padding-right: 0;
    }
  
    .card:nth-child(4n + 1),
    .card:nth-child(4n + 4) {
      flex: 1 0 100%;
      max-width: 100%;
    }
  
    .card-content-container.open {
      padding: 0;
    }
  }
  
  nav {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 300px;
  }
  
  .background {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 300px;
    background: #fff;
  }
  
  .menuButton {
    outline: none;
    border: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    cursor: pointer;
    position: absolute;
    top: 18px;
    left: 15px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: transparent;
  }
  
  ul,
  li {
    margin: 0;
    padding: 0;
  }
  

  
  li {
    list-style: none;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .icon-placeholder {
    font-size: 2px;
    font-weight: 800;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    flex: 40px 0;
    margin-right: 20px;
  }
  
  .text-placeholder {
    font-weight: 800;
    display: flex;
    padding-left: 5%;
    align-items: center;
    text-align: left;
    border-radius: 5px;
    width: 200px;
    height: 30px;
    flex: 1;
  }
  
  .refresh {
    padding: 10px;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    width: 20px;
    height: 20px;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
